.header {
  height: 904px;

  nav {
    margin-right: 17.3%;
    padding-top: 80px;
    display: flex;
    justify-content: flex-end;
    gap: 60px;

    button {
      cursor: pointer;
      font-weight: 300;
      background-color: transparent;
      border: none;
      font-size: 14px;
      line-height: 21px;
      letter-spacing: 1.94px;
      color: #ffffff;

      &::after {
        margin-right: auto;
        position: relative;
        bottom: 0;
        left: 65%;
        background: #4caf50;
        width: 35%;

        content: "";
        display: block;
        height: 2px;
        transition: 0.3s;
      }

      &:hover::after {
        left: -20%;
        width: 130%;
      }
    }

    .active {
      &::after {
        left: -20%;
        width: 130%;
      }
    }
  }

  .headerInfo {
    margin-top: 283px;
    color: #ffffff;
    text-align: center;

    h1 {
      font-weight: 500;
      font-size: 72px;
      line-height: 107px;
      letter-spacing: 4.608px;
      margin: 0;
    }

    p {
      font-family: "Open Sans";
      font-size: 20px;
      line-height: 27px;
      letter-spacing: 1.28px;
      opacity: 0.8;
      mix-blend-mode: normal;
    }
  }

  .paginator {
    text-align: center;
    margin-top: 295px;

    button {
      cursor: pointer;
      border: none;
      background-color: transparent;
      font-weight: bold;
      font-size: 18px;
      line-height: 27px;
      letter-spacing: 1.152px;
      color: rgba(255, 255, 255, 0.437245);
    }

    .active {
      font-size: 36px;
      line-height: 53px;
      letter-spacing: 2.304px;
      color: #ffffff;
    }
  }
}
